#gfg {
    position: relative;
  }
  
  #gfg::before {
    content: "";
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: url('https://i.pinimg.com/originals/0c/1f/74/0c1f748f5702c12423c3720d4cce4cc4.png') center/cover no-repeat; /* Set your background image here */
    opacity: 0.7; /* Set the desired opacity value (0 to 1) */
    z-index: -1;
  }
  
